import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import { Card, Form, Input, Row, Col, message } from 'antd';
import { requiredRule } from 'utils/validateRules';
import { BtnSave, BtnCancel } from 'components/common/button';
import {
  getRecruitingSchoolById,
  createRecruitingSchool,
  updateRecruitingSchool,
} from 'services/recruitingSchool';
import { getQueryParams } from 'utils/urlUtil';

const handleInitialValue = initialValue => {
  const initialValueDefault = {
    id: '',
    label: '',
    isFee: false,
    isTrainExpense: false,
    isRestudy: false,
    isRetest: false,
    amount: 0,
  };
  return { ...initialValueDefault, ...initialValue };
};

@Form.create({ name: 'FormAccountsReceivable' })
class RecruitingSchoolEdit extends Component {
  state = {
    id: '',
    initialValue: {},
  };

  componentDidMount = () => {
    const { id: queryParamId } = getQueryParams();
    if (queryParamId) {
      this.setState({ id: queryParamId });
      this.getDataById(queryParamId);
    }
  };

  getDataById = async id => {
    const res = await getRecruitingSchoolById(id);
    const initialValue = handleInitialValue(res.data);
    this.setState({ initialValue });
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      this.saveData(values);
    });
  };

  saveData = async values => {
    try {
      const { id } = this.state;
      if (!id) {
        await createRecruitingSchool(values);
        await message.success('Tạo mới thành công', 0.5);
      } else {
        await updateRecruitingSchool(values);
        await message.success('Cập nhật thành công', 0.5);
      }
      this.props.history.goBack();
    } catch (err) {
      console.log(err);
    }
  };

  render() {
    const { initialValue } = this.state;
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
      colon: false,
    };
    return (
      <Card title="Cập nhật mã trường tuyển dụng">
        <Row>
          <Col span={24}>
            <Form onSubmit={this.handleSubmit}>
              <Form.Item label="Mã" {...formItemLayout}>
                {getFieldDecorator('id', {
                  initialValue: initialValue.id,
                  rules: [requiredRule],
                })(<Input readOnly={!!initialValue.id} />)}
              </Form.Item>
              <Form.Item label="Tên" {...formItemLayout}>
                {getFieldDecorator('label', {
                  initialValue: initialValue.label,
                  rules: [requiredRule],
                })(<Input />)}
              </Form.Item>
              <Form.Item {...formItemLayout} label=" ">
                <BtnSave htmlType="submit" />
                <BtnCancel onClick={() => this.props.history.goBack()} />
              </Form.Item>
            </Form>
          </Col>
        </Row>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <RecruitingSchoolEdit {...props} />
  </Page>
));



